import React, {useCallback} from "react";
import styled from "styled-components";
import LogoImg from "../assets/imgs/logo@2x.png"
import {scroller} from "react-scroll";

export const Headner: React.FC<{ opacity: number, ac: number }> = ({opacity, ac}) => {

    const goToFn = useCallback((ev: string) => {
        scroller.scrollTo(ev, {
            duration: 500,
            delay: 100,
            smooth: 'easeInOutQuint',
        })
    }, [])

    return (
        <WapStyle style={{backgroundColor: `RGBA(0, 0, 0, ${opacity})`}}>
            <div className="innder">
                <div className="logo">
                    <img src={LogoImg} alt=""/>
                </div>
                <div className="right">
                    <p onClick={() => goToFn("test1")} className={ac === 0 ? 'ac' : ""}>About us</p>
                    <p onClick={() => goToFn("test2")} className={ac === 1 ? 'ac' : ""}>Portfolio</p>
                    <p onClick={() => {
                        window.open("https://twitter.com/CaballerosCap", "_black")
                    }}>Twitter</p>
                </div>
            </div>

        </WapStyle>
    );
}

const WapStyle = styled.div`
  position: fixed;
  top: 0;
  z-index: 10000;
  width: 100vw;

  .innder {
    max-width: 1100px;
    margin: 0 auto;
    transition: all 1s;
    padding: 8px 5%;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .logo {
      width: 124px;
      height: 46px;

      img {
        display: block;
        width: 100%;
      }
    }

    .right {
      color: #E0E0E0;
      font-size: 16px;
      display: flex;

      p {
        margin-left: 32px;
        cursor: pointer;
        padding: 5px 0;
        transition: all 1s;
      }

      .ac {
        border-bottom: 2px solid #C11C1C;
      }
    }
  }
`
