import React, {useCallback, useEffect, useState} from 'react';
import './App.css';
import {Headner} from "./widgets/headner";
import {Banner} from "./widgets/banner";
import {Randoms} from "./widgets/randoms";
import {BottomWidget} from "./widgets/bottom";
import {Element} from 'react-scroll';


function App() {

    const [startTransform, setStart] = useState(false)
    const [opacity, setOpacity] = useState(0.3)
    const [ac, setAc] = useState(0)

    const handleScroll = useCallback((ev) => {
        let scrollTop = document.documentElement.scrollTop;
        let scrollHeight = document.documentElement.scrollHeight;
        let clientHeight = document.documentElement.clientHeight;

        if (clientHeight + scrollTop > scrollHeight - 50) {
            setStart(true)
        }

        if (scrollTop > (clientHeight * 0.3) && scrollTop < clientHeight) {
            setOpacity((scrollTop / clientHeight) - 0.2)
        }
        setAc(scrollTop > clientHeight - 40  ? 1 : 0);

    }, [])
    useEffect(() => {
        window.addEventListener('scroll', handleScroll);

        return () => window.removeEventListener('scroll', handleScroll);
    }, [])

    return (
        <div className="App">
            <Headner opacity={opacity} ac={ac}/>
            <Element name="test1" className="element"/>

            <Banner/>
            <Element name="test2" className="element"/>

            <Randoms/>
            <BottomWidget start={startTransform}/>
        </div>
    );
}

export default App;
