import React, {useCallback, useEffect, useState} from "react";
import styled from "styled-components";

let i = false;
export const BottomWidget: React.FC<{ start: boolean }> = ({start}) => {
    let [ac, setAc] = useState(0);
    const listText = ['Contact', 'Los', 'Caballeros']

    const twFn = useCallback(() => {
        if (ac > 2) {
            return;
        }
        setTimeout(() => {
            setAc(ac += 1);
            twFn();
        }, 200)
    }, [ac])

    useEffect(() => {
        if (start && !i) {
            twFn();
            i = true;
        }
    }, [start])

    return (
        <WapStyle href="mailto:contact@caballeros.capital">
            {
                listText.map((ev, i) => <span key={ev} className={ac > i ? 'ev' : ''}>{ev}</span>)
            }
        </WapStyle>
    );
}


const WapStyle = styled.a`
  font-size: 16px;
  font-family: PlayfairDisplay-Regular, PlayfairDisplay;
  font-weight: 400;
  color: #FFFFFF;
  line-height: 21px;
  padding: 40px 0;
  display: flex;
  justify-content: center;

  span {
    margin: 0 5px;
    transition: transform 1s;
    transform: rotate3d(1, 0, 0, 90deg);
  }

  .ev {
    transform: rotate3d(1, 0, 0, 0deg);
  }
`
